<svelte:options tag="fds-image-editor-dialog" />

<script>
    import { onMount, beforeUpdate, createEventDispatcher } from 'svelte'
    import { fade, fly } from 'svelte/transition'
    import { quintOut } from 'svelte/easing'
    import { get_current_component } from 'svelte/internal'
    import PoseNetSelect  from './PoseNetSelect.svelte'
    import ShapeSelect  from './ShapeSelect.svelte'
    import SearchShapes  from './SearchShapes.svelte'
    import SearchModels3d  from './SearchModels3d.svelte'
    import Presets from "./Presets.svelte"
   // import Pres from "./PresetsOld.svelte"
    import Assets from "./Assets.svelte"
    import TestAssetPlugin from "./TestAssetPlugin.svelte"
    import VideoAsset from "./VideoAsset.svelte"

    let mypopover;
    let host = get_current_component().$$.root.host
    export let dialogelement;
    export let nobackdrop;
    let svelteDispatch = createEventDispatcher()
    const dispatch = (name, detail) => {
        svelteDispatch(name, detail)
        host.dispatchEvent && host.dispatchEvent(new CustomEvent(name, { detail }))
    }

    const noop = () => {}
    /**
     * set to true to open dialog or to false to close it
     * @type {boolean}
     */
    export let open = false
    export let usepopover= null;
    export let fullscreenmode=null;
    /**
     * Additionaly clases for dialog
     * @type {string}
     */
    export let dialogClasses = ''
    /**
     * if background around dialog should be visible
     * @type {boolean}
     */
    export let backdrop = true
    /**
     * if clicking outside dialog close the dialog
     * @type {boolean}
     */
    export let ignorebackdrop = true
    /**
     * activate keys to control dialog
     * @type {boolean}
     */
    export let keyboard = false
    /**
     * is used to indicate the IDs of the elements that describe the object
     * @type {string}
     */
    export let describedby = ''
    /**
     * attribute establishes relationships between objects and their label(s)
     * @type {string}
     */
    export let labelledby = ''
    /**
     * callback run on dialog open
     * @type {function}
     */
    export let onOpened = noop
    /**
     * strignified callback run on dialog open
     * @type {string | function}
     */
    export let onopened = ''

    /**
     * callback run on dialog close
     * @type {function}
     */
    export let onClosed = noop
    /**
     * strignified callback run on dialog close
     * @type {string|function}
     */
    export let onclosed = ''
    /**
     * Determine if dialog has title element with close button
     * @type {boolean}
     */
    export let hasheader = true

    /**
     * callback when dialog is displayed
     * @type {function}
     */
    export let onShown = noop
    /**
     * strignified callback when dialog is displayed
     * @type {string|function}
     */
    export let onshown = ''

    /**
     * html or text content of dialog
     * @type {string|html}
     */
    export let modalcontent = ''
    /**
     * dom element of content element, you can addchild to it instead of set modalcontent
     * @type {HTMLElement}
     */
    export let contentelement = null
    /**
     * dom element of title element, you can addchild to it instead of set modaltitle
     * @type {HTMLElement}
     */
    export let titleelement = null
    /**
     * dom element of footer element, you can addchild to it instead of set modalfooter
     * @type {HTMLElement}
     */
    export let footerelement = null
    /**
     * content of footer of dialog
     * @type {string}
     */
    export let modalfooter = ''
    /**
     * content of title of dialog
     * @type {string}
     */
    export let modaltitle = ''
    /**
     * size(type) of dialog
     * @type {'xs'|'sm'|'lg'|'xl'}
     */
    export let size = ''
    /**
     * width of dialog
     * @type {string}
     */
    export let width = '600px'
    /**
     * move fds-dialog to body, to avoid event stealing from parent component
     * @type {boolean}
     */
    export let movetobody = false

    /**
     * move fds-dialog to body, to avoid event stealing from parent component
     * @type {string}
     */
    export let design = null

    export let onscroll = () => {}
    export let maxheight = null
    export let showclose = true
    const component = get_current_component()
    let state = null
    let _keyboardEvent
    let styleel = null
    let fullsizeClass
    let initialized = false
    let movedtobody
    let fullscreenClass="";
    $: if (showclose === 'false') showclose = false

    $: {
        if (dialogelement && window.browserwidth) {
            dialogelement.style.left = '150px'
            dialogelement.style.right = 'initial'
        }
    }

    $: {
        if (maxheight && contentelement) {
            let mhval = `${maxheight}`
            contentelement.style.maxHeight = mhval
        }
    }

    $: {
        if (movetobody === 'false') movetobody = false
        movetobody = !!movetobody
    }
    // Watching changes for Open vairable
    $: {
        if (open && document.body) {
            modalOpen()
        } else if (document.body) {
            modalClose()
        }
    }
    $: if(open && mypopover && initialized && usepopover){
        mypopover.showPopover();
    }
    $: {
        if (size === 'xs') width = '150px'
        if (size === 'sm') width = '300px'
        if (size === '') width = '600px'
        if (size === 'lg') width = '900px'
        if (size === 'xl') width = '1200px'
        if (size === 'xxl') width = '1600px'
        fullsizeClass = ''

        if (size === 'fullsize') {
            fullsizeClass = 'fullsize'
        }
    }
    $: {
        if(fullscreenmode){
            fullscreenClass = 'fullScreen'
        } else{
            dialogClasses = dialogClasses+" modal-dialog";
        }
    }

    $: if (open && initialized && !movedtobody) {  }

    $: if (dialogelement && movetobody){
        dialogelement.style.zIndex = "2000";
    }

        $:if (movetobody && component && component.$$.root.host.parentNode && component.$$.root.host.parentNode !== document.body) {
            //document.body.appendChild( component.$$.root.host)
        }
    

    /**
     * Show dialog with properties object
     * @param configobj
     */
    export function show(configobj) {
        configObjtoLocalVars(configobj)
        open = true
    }

    /**
     * close dialog
     */
    export function close() {
        open = false
    }

    function configObjtoLocalVars(configobj) {
        if ('open' in configobj) open = configobj.open
        if ('dialogClasses' in configobj) dialogClasses = configobj.dialogClasses
        if ('ignorebackdrop' in configobj) ignorebackdrop = configobj.ignorebackdrop
        if ('onOpened' in configobj) onOpened = configobj.onOpened
        if ('onClosed' in configobj) onClosed = configobj.onClosed
        if ('onshown' in configobj) onshown = configobj.onshown
        if ('modalcontent' in configobj) modalcontent = configobj.modalcontent
        if ('modalfooter' in configobj) modalfooter = configobj.modalfooter
        if ('modaltitle' in configobj) modaltitle = configobj.modaltitle
    }

    function setStateFromLocal() {
        state = {
            open,
            dialogClasses,
            backdrop,
            ignorebackdrop,
            keyboard,
            describedby,
            labelledby,
            onOpened,
            onShown,
            onClosed,
            modalcontent,
            modalfooter,
            modaltitle,
        }
    }

    beforeUpdate(() => {
        if (styleel) {
            if (design && design === 'clear') {
                styleel.innerHTML += ' .modal-content { border-radius: 0 !important; border: none; box-shadow: none; background-color: transparent}'
                styleel.innerHTML += ' .close { color: white; opacity: 1;}'
                styleel.innerHTML += ' .cross { font-family: auto; font-size: 32px; }'
                styleel.innerHTML += ' .modal-body {  padding: 0; max-height: none}'
            }
        }
    })

    onMount(() => {
        initialized = true
        setStateFromLocal()
    })

    function attachEvent(target, ...args) {
        target.addEventListener(...args)
        return {
            remove: () => target.removeEventListener(...args),
        }
    }

    function checkClass(className) {
        if (!className) return
        return document.body.classList.contains(className)
    }

    function modalOpen() {
        if (!checkClass('modal-open')) {
            document.body.classList.add('modal-open')
        }
    }
    function modalClose() {
        if (checkClass('modal-open')) {
            document.body.classList.remove('modal-open')
        }
    }

    function handleBackdrop(event) {
        if (backdrop && !ignorebackdrop) {
            event.stopPropagation()
            open = false
        }
    }

    /**
     * Will be fired if  dialog is open
     * @event open
     */
    /**
     * Will be fired if shown
     * @event show
     */

    function onModalOpened() {
        if (keyboard) {
            _keyboardEvent = attachEvent(document, 'keydown', (e) => {
                if (event.key === 'Escape') {
                    open = false
                }
            })
        }

        if ((typeof onopened === 'string' || onopened instanceof String) && onopened) {
            eval(onopened)()
        }

        if (typeof onopened === 'function') {
            onopened(component)
        }
        onOpened(component)
        dispatch('open', {})

        if ((typeof onshown === 'string' || onshown instanceof String) && onshown) {
            eval(onshown)()
        }
        if (typeof onshown === 'function') {
            onshown(component)
        }
        onShown(component)
        dispatch('show', {})
    }

    /**
     * Will be fired if modal is closed
     * @event close
     */
    function onModalClosed() {
        if (_keyboardEvent) {
            _keyboardEvent.remove()
        }
        if ((typeof onclosed === 'string' || onclosed instanceof String) && onclosed) {
            eval(onclosed)()
        }
        if (typeof onclosed === 'function') {
            onclosed(component)
        }
        onClosed()
        dispatch('close', {})
    }

    function fixfade(node, params) {
        Object.defineProperty(node, 'ownerDocument', {
            get: function () {
                return { head: node.parentNode }
            },
        })
        return fade(node, params)
    }
</script>

{#if open && initialized}
    <!-- svelte-ignore a11y-click-events-have-key-events -->
    <!-- svelte-ignore a11y-no-noninteractive-element-interactions -->

        <div   bind:this={mypopover} popover={usepopover}>

        <div
            bind:this={dialogelement}
            style="--width: {width};"
            class="modal show {fullsizeClass} {fullscreenClass}"
            tabindex="-1"
            role="dialog"
            aria-labelledby={labelledby}
            aria-describedby={describedby}
            aria-modal="true"
            on:click|self={handleBackdrop}
            on:introend={onModalOpened}
            on:outroend={onModalClosed}
            transition:fixfade
        >
            <div
                style=""
                class="{dialogClasses}"
                role="document"
                in:fly={{ y: -50, duration: 300 }}
                out:fly={{ y: -50, duration: 300, easing: quintOut }}
            >
                <div class="modal-content">
                    {#if !design && design !== 'clear'}
                        {#if hasheader}
                            <div class="modal-header">
                                <h5 bind:this={titleelement} class="modal-title">
                                    <slot name="modaltitle">{@html modaltitle}</slot>
                                </h5>
                                {#if showclose}
                                    <fds-image-editor-button icon="closeBtn" title="Close" on:click={() => (open = false)} class="iconTopRight" />
                                {/if}
                            </div>
                        {/if}
                    {:else if showclose}
                        <fds-image-editor-button icon="closeBtn" title="Close" on:click={() => (open = false)} class="iconTopRight" />
                    {/if}
                    <div bind:this={contentelement} class="modal-body" on:scroll={(ev) => onscroll(ev)}>
                        <slot name="modalcontent">{@html modalcontent}</slot>
                    </div>
                    {#if modalfooter}
                        <div bind:this={footerelement} class="modal-footer">
                            <slot name="modalfooter">{@html modalfooter}</slot>
                        </div>
                    {/if}
                </div>
            </div>
        </div>
       </div>



    {#if !nobackdrop && open}
        <div class="modal-backdrop show" transition:fixfade={{ duration: 150 }} />
    {/if}
{/if}

<style>
    .modal {
        display: none;
        overflow: hidden;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1050;
        -webkit-overflow-scrolling: touch;
        outline: 0;
        background-color: #00000063;
        padding-top: 2%;
    }
    .modal.fullsize {
        padding-top: 0;
    }
    .modal-dialog {
        position: relative;
        width: auto;
        margin: 10px;
    }
    .modal.fullsize .modal-dialog {
        margin: 0;
        width: 100vw;
    }
    .modal-content {
        position: relative;
        background-color: var(--dialog-bgcolor);
        color: var(--text-color);
        border: 1px solid rgba(0, 0, 0, 0.2);
        border-radius: 6px;
        -webkit-box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
        box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
        -webkit-background-clip: padding-box;
        background-clip: padding-box;
        outline: 0;
    }
    .modal.fullsize .modal-content {
        height: 100vh;
    }
    .modal-backdrop {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1040;
        background-color: #000000;
    }
    .modal-header {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: start;
        align-items: flex-start;
        -ms-flex-pack: justify;
        justify-content: space-between;
        border-top-left-radius: calc(0.3rem - 1px);
        border-top-right-radius: calc(0.3rem - 1px);
    }
    .modal-header .close {
        margin-top: -2px;
        margin-right: 5px;
    }

    .modal-body {
        position: relative;
        padding: 15px;
        max-height: calc(100vh - 25px);
        /*max-height: calc(max(1200px, 100vh - 250px));*/
        overflow-y: auto;
    }

    .fullsize .modal-body {
        height: var(--modal-body-height, calc(100vh - 25px));
        max-height: none;
        overflow-y: scroll;
    }

    .modal-footer {
        padding: 15px;
        text-align: right;
        border-top: 1px solid #e5e5e5;
        width: 100%;
        height: auto;
        box-sizing: border-box;
    }

    @media (min-width: 768px) {
        .modal-dialog {
            max-width: var(--width, 600px);
            margin: 30px auto;
        }
        .modal-content {
            -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
        }
    }

    .show {
        display: block !important;
    }
    .modal-header .close {
        cursor: pointer;
    }
    .modal-title {
        margin: 0;
        line-height: 1.5;
        text-align: center;
        padding: 5px;
        width: 100%;
        display: block;
        font-weight: 300;
    }
    h5 {
        font-size: 1.25em;
    }
    .modal {
        font-size: var(--font-size, 16px);
        font-weight: 400;
        line-height: 1.5;
        color: #212529;
        text-align: left;
    }
    .modal-backdrop.show {
        opacity: 0.8;
    }

    .fullScreen {

        width: calc(100vw - 25px);
        height: calc(100vh - 25px);

        display: block;
        z-index: 3000;
        box-shadow: 0 0 1rem 0 rgba(255, 255, 255, .2);
        border-radius: 5px;
        background-color: rgba(0, 0, 0, .65);
        backdrop-filter: blur(10px);
        color: rgba(255, 255, 255, 0.6);

        /*position: absolute;*/
        left: 0px;
        top: 0px;
        padding: 10px;
        font-family: "Segoe UI", Roboto, system-ui;

    }
</style>
