<svelte:options customElement="fds-assets-inline"></svelte:options>

<script>
  import { createEventDispatcher } from 'svelte';
  import Sortable from 'sortablejs';

  const dispatch = createEventDispatcher();

  // Props
  export let assets = [];
  export let select_filter_callback = null; // Falls definiert, sind Assets auswhlbar
  export let on_select_callback = null; // Optionaler Callback bei Auswahl
  export let inline = false; // Inline-Modus (verndert das Select-Verhalten)

  let dragStatus = 'none'
  let repeaterElement;
  let currentPath = []; // Array von Ordner-IDs (fr Navigation)
  let sortableInstance = null;
  // Der aktuelle Ordner basiert auf assets und currentPath
  let currentFolder = { children: assets };

  let showPropertiesDialog = false;
  let selectedAsset = null;
  let editName = '';
  let editDescription = '';

  // Modi: "normal" (Default) oder "reorder"
  let mode = 'normal';
  
  // View mode: "project" (Default) oder "all"
  export let viewMode = 'project';

  // Wird im Drag & Drop (normal mode) verwendet
  let draggedAsset = null;

  // Fr den Folder-Up-Button (Drop-Feedback)
  let folderUpHover = false;
  let folderUpDragCount = 0;

  // Reaktive Aktualisierung des aktuellen Ordners
  $: updateCurrentFolder(currentPath, assets);
  async function updateCurrentFolder(path, assets) {
    
    if (!Array.isArray(assets)) {
      console.error("assets is not an array!", { assets });
      assets = [];
    }
    
    let folder = { children: assets };
    try {
      for (const fid of path) {
        folder = folder.children.find(c => c.id === fid);
        if (!folder) throw new Error('Invalid path');
      }
    } catch {
      currentPath = [];
      folder = { children: assets };
    }
    
    // Ensure folder has children array
    if (!folder.children) folder.children = [];
    
    // Make sure all children have an id
    folder.children = folder.children.filter(asset => asset && asset.id);
    
    // Process each asset with its plugin before displaying
    if (folder && folder.children) {
      for (const asset of folder.children) {
        if (asset.type && asset.type !== "folder") {
          await processWithPlugin(asset.type, 'initAssetsOverview', asset);
        }
      }
    }
    
    // CRITICAL: Wenn wir im Root-Ordner sind, mssen wir sicherstellen, 
    // dass currentFolder.children und assets auf das gleiche Array zeigen
    if (path.length === 0) {
      // Im Root-Ordner zeigen currentFolder.children und assets auf das gleiche Array
      folder.children = assets;
    }
    
    
    currentFolder = folder;
  }

  // Navigation: Eine Ebene nach oben
  function navigateUp() {
    if (currentPath.length > 0) {
      currentPath = currentPath.slice(0, -1);
    }
  }
  // Erweitert den Pfad um die angegebene Folder-ID
  function navigateToFolder(folderId) {
    currentPath = [...currentPath, folderId];
  }

  // Hilfsfunktion fr Bildstile
  function getImageStyle(meta) {
    if (meta?.style) return meta.style;
    if (!meta || !meta.width) return "width: auto; height: 112px;";
    if (meta.width === meta.height) return "height: 112px; width: 112px;";
    if (meta.width > meta.height) return "width: 200px; height: auto;";
    return "width: auto; height: 112px;";
  }

  // File Upload Handler
  function handleFileDragOver(event) {
    if (!event.dataTransfer?.items?.length) return;
    const hasFiles = Array.from(event.dataTransfer.items).some(item => item.kind === 'file');
    if (!hasFiles) return;
    event.preventDefault();
    const hasCompatibleFiles = Array.from(event.dataTransfer.items).some(item => checkAgainstPlugins(item));
    dragStatus = hasCompatibleFiles ? 'valid' : 'invalid';
  }
  function handleFileDragLeave(event) {
    event.preventDefault();
    dragStatus = 'none';
  }
  async function handleFilesDrop(event) {
    // Check if the drop target is a folder item (asset with type "folder")
    // If so, let the folder's drop handler handle it
    const targetElement = event.target.closest('.draggable-asset');
    const assetId = targetElement?.dataset?.id;
    if (assetId) {
      const asset = currentFolder.children.find(a => a.id === assetId);
      if (asset && asset.type === "folder") {
        // Let the folder's drop handler work
        return;
      }
    }
    
    event.preventDefault();
    event.stopPropagation();
    dragStatus = 'none';
    const files = Array.from(event.dataTransfer.files);
    for (const file of files) {
      const matchingPlugins = [];
      for (let tag in globalThis.gyre.plugins) {
        const pluginInfo = globalThis.gyre.plugins[tag].options;
        if (pluginInfo.type === "asset" && checkFileType(file, pluginInfo.allowedTypes)) {
          matchingPlugins.push({
            tag,
            name: pluginInfo.name || tag,
            description: pluginInfo.description || ""
          });
        }
      }
      if (matchingPlugins.length === 0) continue;
      if (matchingPlugins.length === 1) {
        await processWithPlugin(matchingPlugins[0].tag, 'importFileAsAsset', file, currentFolder);
        currentFolder = currentFolder; // Trigger Update
        continue;
      }
      let selectedTag = await openMenu(event, matchingPlugins.map(p => ({
        id: p.tag,
        name: p.description ? `${p.name} (${p.description})` : p.name,
        key: p.tag
      })));
      await processWithPlugin(selectedTag, 'importFileAsAsset', file, currentFolder);
      currentFolder = currentFolder; // Trigger Update
    }
  }

  let selectedMenuItem = {};
  async function openMenu(event, items, element = null) {
    let doc = window.document;
    const contextMenu = doc.createElement("fds-image-editor-menu");
    contextMenu.menu = items;
    if (element) {
      contextMenu.element = element;
    } else {
      contextMenu.x = event.clientX;
      contextMenu.y = event.clientY;
    }
    doc.body.appendChild(contextMenu);
    const selection = await new Promise(resolve => {
      contextMenu.callback = (item) => {
        doc.body.removeChild(contextMenu);
        selectedMenuItem = item;
        resolve(item.key);
      }
    });
    return selection;
  }

  // Neues Asset hinzufgen
  async function add(event) {
    let items = [
      { id: 1, name: "Upload Media...", key: "upload" },
      { id: 2, name: "Add Folder", key: "folder" }
    ];
    let id = 3;
    for (let tag in globalThis.gyre.plugins) {
      const pluginInfo = globalThis.gyre.plugins[tag].options;
      if (pluginInfo.type === "asset" && pluginInfo.createEmpty) {
        items.push({ name: "New " + pluginInfo.name, id: id++, key: "new_" + tag, tag });
      }
    }
    let selection = await openMenu(event, items);
    if (selection === "folder") {
      let folder = {
        name: "New Folder",
        id: crypto.randomUUID(),
        type: "folder",
        meta: { style: "width: 50px; height: 50px;" },
        children: [],
        thumbnail: 'data:image/svg+xml;base64,'
      };
      currentFolder.children.push(folder);
      currentFolder = currentFolder;
      return;
    }
    if (selection.startsWith("new_")) {
      await processWithPlugin(selectedMenuItem.tag, 'createEmptyAsset', currentFolder);
      currentFolder = currentFolder;
    }
  }
  async function deleteAsset(asset) {
    let res=confirm("Are you sure?")
    if (!res) return
    await processWithPlugin(asset.type, 'deleteAsset', asset)
    const index = currentFolder.children.indexOf(asset)
    if (index !== -1) {
      currentFolder.children.splice(index, 1)
    }    
    currentFolder = currentFolder
  }
  // Kontextmen fr ein Asset (ohne "Move...")
  async function assetSettingsMenu(event, asset) {
    let pluginInfo = {};
    if (asset.type !== "folder") pluginInfo = globalThis.gyre.plugins[asset.type].options;
    let items = [
      { id: 1, name: "Delete", key: "delete" },
      { id: 2, name: "Properties", key: "meta" }
    ];
    if (pluginInfo.hasClone) {
      items.push({ id: 3, name: "Clone", key: "clone" });
    }
    if (pluginInfo.contextMenu && pluginInfo.contextMenu.length) {
      for (let i = 0; i < pluginInfo.contextMenu.length; i++) {
        let entry = pluginInfo.contextMenu[i];
        items.push({ id: i + 4, name: entry.name, key: entry.key, method: entry.method });
      }
    }
    let key = await openMenu(event, items);
    if (key === "delete") return await deleteAsset(asset);
    if (key === "meta") {
      selectedAsset = asset;
      editName = asset.name;
      editDescription = asset.description || '';
      showPropertiesDialog = true;
      return;
    }
    if (key === "clone") {
      await processWithPlugin(asset.type, 'cloneAsset', asset, currentFolder);
      currentFolder = currentFolder;
      return;
    }
    let entry = selectedMenuItem;
    await processWithPlugin(asset.type, entry.method, asset, currentFolder);
    currentFolder = currentFolder;
  }

  function saveProperties() {
    if (selectedAsset) {
      selectedAsset.name = editName;
      selectedAsset.description = editDescription;
      currentFolder.children = currentFolder.children;
    }
    showPropertiesDialog = false;
  }

  function checkAgainstPlugins(file) {
    for (let tag in globalThis.gyre?.plugins) {
      let pluginInfo = globalThis.gyre.plugins[tag].options;
      if (pluginInfo.type === "asset") {
        if (checkFileType(file, pluginInfo.allowedTypes)) {
          return true;
        }
      }
    }
    return false;
  }
  function checkFileType(file, allowedTypes) {
    return allowedTypes.some(type => {
      if (type.startsWith('.') && file.name?.toLowerCase().endsWith(type)) {
        return true;
      }
      return file.type === type;
    });
  }

  // Entfernt ein Asset mit der gegebenen ID rekursiv aus dem Array (z.B. assets)
  function removeAssetById(list, id) {
    for (let i = 0; i < list.length; i++) {
      if (list[i].id === id) {
        list.splice(i, 1);
        return true;
      } else if (list[i].children) {
        if (removeAssetById(list[i].children, id)) {
          return true;
        }
      }
    }
    return false;
  }

  async function processWithPlugin(tag, methodName, ...methodParams) {
    const doc = window.document;
    let pluginComponent = doc.body.querySelector(`:scope > ${tag}`);
    let isNewComponent = false;
    if (!pluginComponent) {
      pluginComponent = doc.createElement(tag);
      doc.body.appendChild(pluginComponent);
      isNewComponent = true;
    }
    await pluginComponent.ready;
    if (!pluginComponent[methodName]) return
    let res = await pluginComponent[methodName](...methodParams);
    if (isNewComponent) {
      doc.body.removeChild(pluginComponent);
    }
    return res;
  }

  async function openAsset(asset) {
    if (asset.type === "folder") {
      navigateToFolder(asset.id);
      return;
    }
    asset = globalThis.gyre.assetManager.getAssetById(asset.id);
    if (!globalThis.gyre.plugins[asset.type]) {
      alert("openAsset: Plugin " + asset.type + " not found");
      return;
    }
    let pluginInfo = globalThis.gyre.plugins[asset.type].options;
    if (pluginInfo.openAsset === "off" || pluginInfo.openAsset === "none") return;
    if (pluginInfo.openAsset === "default") {
      if (asset.isOpen) {
        globalThis.broadcast.sendWithoutResponse("focus", null, asset.id);
        return;
      }
      window.open('./asset.html?assetId=' + asset.id + "&docId=" + globalThis.gyre.docId, asset.id);
      return;
    }
    await processWithPlugin(asset.type, 'openAsset', asset);
  }

  /* Sortable fr "Re-Order"-Mode */
  function initializeSortable() {
    if (sortableInstance) sortableInstance.destroy();
    
    console.log("INIT SORTABLE", {
      currentChildren: currentFolder.children?.length || 0,
      assets: assets?.length || 0,
      globalAssets: globalThis.gyre?.assets?.length || 0
    });
    
    sortableInstance = Sortable.create(repeaterElement, {
      animation: 200,
      easing: "cubic-bezier(1, 0, 0, 1)",
      ghostClass: 'sortable-ghost',
      dragClass: 'sortable-drag',
      chosenClass: 'sortable-chosen',
      // Nur Elemente mit der Klasse "draggable-asset" (d.h. Assets) werden sortierbar
      draggable: '.draggable-asset',
      forceFallback: false,
      fallbackClass: 'sortable-fallback',
      onMove: function(evt) {
        return !evt.related.classList.contains('menu-item');
      },
      onStart: (evt) => {
        console.log("DRAG START", {
          currentChildren: currentFolder.children?.length || 0,
          assets: assets?.length || 0,
          globalAssets: globalThis.gyre?.assets?.length || 0
        });
      },
      onEnd: (evt) => {
        console.log("DRAG END BEFORE", {
          oldIndex: evt.oldIndex,
          newIndex: evt.newIndex,
          currentChildren: currentFolder.children?.length || 0,
          assets: assets?.length || 0,
          globalAssets: globalThis.gyre?.assets?.length || 0
        });
        
        // Nur ausfhren, wenn evt gltig und oldIndex und newIndex existieren
        if (evt && evt.oldIndex !== undefined && evt.newIndex !== undefined && evt.oldIndex !== evt.newIndex) {
          try {
            // Sicherstellen, dass die Indizes gltig sind
            if (evt.oldIndex >= 0 && evt.oldIndex < currentFolder.children.length &&
                evt.newIndex >= 0 && evt.newIndex < currentFolder.children.length) {
              
              // backup machen
              const backupChildren = [...currentFolder.children];
              
              // Element verschieben
              const item = currentFolder.children.splice(evt.oldIndex, 1)[0];
              currentFolder.children.splice(evt.newIndex, 0, item);
              
              // Prfen, ob die Lnge gleich geblieben ist
              if (currentFolder.children.length !== backupChildren.length) {
                console.error("Lnge hat sich verndert! Backup wiederherstellen");
                currentFolder.children = backupChildren;
                return;
              }
              
              // Debug nach dem Verschieben
              console.log("AFTER MOVE", {
                currentChildren: currentFolder.children?.length || 0,
                assets: assets?.length || 0,
                globalAssets: globalThis.gyre?.assets?.length || 0,
              });
              
              // WICHTIG: Hier muss die ursprngliche assets-Referenz erhalten bleiben
              if (currentPath.length === 0) {
                // SAFETY CHECK
                if (assets === undefined || assets === null) {
                  console.error("assets undefined or null!");
                  return;
                }
                
                // Root-Ordner: assets muss aktiv aktualisiert werden
                const tempAssets = [...currentFolder.children]; // Kopie erstellen
                
                // Sicherstellen, dass wir nicht durch die Referenzzuweisung das Array verlieren
                assets.length = 0; // Leeren, aber Referenz behalten
                tempAssets.forEach(item => assets.push(item)); // Neu befllen
                
                // Log-Ausgabe nach assets-Update
                console.log("AFTER ASSETS UPDATE", {
                  assetsLength: assets?.length || 0,
                  assetsJsIds: assets?.map(a => a.id) || []
                });
                
                // Event auslsen
                dispatch('assetsChanged', { assets });
                
                // globalThis.gyre.assets aktualisieren
                if (globalThis.gyre?.assets) {
                  const gyreAssets = globalThis.gyre.assets;
                  gyreAssets.length = 0;
                  tempAssets.forEach(a => gyreAssets.push(a));
                  
                  console.log("AFTER GYRE UPDATE", {
                    gyreAssetsLength: gyreAssets?.length || 0,
                    gyreAssetJsIds: gyreAssets?.map(a => a.id) || []
                  });
                }
              }
            }
          } catch (error) {
            console.error("Error in sortable onEnd:", error, error.stack);
          }
        }
      }
    });
  }

  $: {
    if (mode === 'reorder' && repeaterElement) {
      initializeSortable();
    } else if (sortableInstance) {
      sortableInstance.destroy();
      sortableInstance = null;
    }
  }

  /* Drag & Drop Handler fr "Normal"-Mode */
  function handleDragStart(event, asset) {
    draggedAsset = asset;
    event.dataTransfer.effectAllowed = "move";
    event.dataTransfer.setData("text/plain", asset.id);
  }
  function handleDragOverItem(event, asset) {
    if (mode !== 'normal') return;
    if (event.dataTransfer.files && event.dataTransfer.files.length > 0) return;
    event.preventDefault();
    event.dataTransfer.dropEffect = "move";
  }
  function handleDragEnter(event, asset) {
    if (mode !== 'normal') return;
    if (event.dataTransfer.files && event.dataTransfer.files.length > 0) return;
    asset.dragCount = (asset.dragCount || 0) + 1;
    asset.isDropTarget = true;
    currentFolder = currentFolder;
  }
  function handleDragLeave(event, asset) {
    if (mode !== 'normal') return;
    if (event.dataTransfer.files && event.dataTransfer.files.length > 0) return;
    asset.dragCount = (asset.dragCount || 0) - 1;
    if (asset.dragCount <= 0) {
      asset.isDropTarget = false;
      asset.dragCount = 0;
    }
    currentFolder = currentFolder;
  }
  function handleDropItem(event, asset) {
    if (mode !== 'normal') return;
    event.preventDefault();
    
    // Check if this is a file drop or an asset drag & drop
    if (event.dataTransfer.files && event.dataTransfer.files.length > 0) {
      // This is a file drop into a folder
      const files = Array.from(event.dataTransfer.files);
      handleFilesDropIntoFolder(files, asset);
      return;
    }
    
    // This is an asset drag & drop
    if (draggedAsset && draggedAsset.id !== asset.id) {
      removeAssetById(assets, draggedAsset.id);
      if (!asset.children) {
        asset.children = [];
      }
      asset.children.push(draggedAsset);
      draggedAsset = null;
      currentFolder = currentFolder;
      assets = assets;
    }
    asset.isDropTarget = false;
    asset.dragCount = 0;
  }
  
  // Handle files dropped directly into a folder
  async function handleFilesDropIntoFolder(files, targetFolder) {
    for (const file of files) {
      const matchingPlugins = [];
      for (let tag in globalThis.gyre.plugins) {
        const pluginInfo = globalThis.gyre.plugins[tag].options;
        if (pluginInfo.type === "asset" && checkFileType(file, pluginInfo.allowedTypes)) {
          matchingPlugins.push({
            tag,
            name: pluginInfo.name || tag,
            description: pluginInfo.description || ""
          });
        }
      }
      if (matchingPlugins.length === 0) continue;
      if (matchingPlugins.length === 1) {
        await processWithPlugin(matchingPlugins[0].tag, 'importFileAsAsset', file, targetFolder);
        continue;
      }
      
      // Create a synthetic event with position for menu
      const menuEvent = {
        clientX: window.innerWidth / 2,
        clientY: window.innerHeight / 2
      };
      
      let selectedTag = await openMenu(menuEvent, matchingPlugins.map(p => ({
        id: p.tag,
        name: p.description ? `${p.name} (${p.description})` : p.name,
        key: p.tag
      })));
      await processWithPlugin(selectedTag, 'importFileAsAsset', file, targetFolder);
    }
    
    // Force update
    currentFolder = currentFolder;
  }
  function handleDragEnd(event) {
    if (mode !== 'normal') return;
    draggedAsset = null;
  }

  /* Handler fr den Folder-Up-Button */
  function folderUpVisible() {
    return currentPath.length > 0;
  }
  function handleFolderUpDragOver(event) {
    if (mode !== 'normal') return;
    event.preventDefault();
    event.dataTransfer.dropEffect = "move";
  }
  function handleFolderUpDragEnter(event) {
    if (mode !== 'normal') return;
    folderUpDragCount++;
    folderUpHover = true;
  }
  function handleFolderUpDragLeave(event) {
    if (mode !== 'normal') return;
    folderUpDragCount--;
    if (folderUpDragCount <= 0) {
      folderUpHover = false;
      folderUpDragCount = 0;
    }
  }
  function handleFolderUpDrop(event) {
    if (mode !== 'normal') return;
    event.preventDefault();
    if (event.dataTransfer.files && event.dataTransfer.files.length > 0) return;
    if (draggedAsset) {
      removeAssetById(assets, draggedAsset.id);
      let parent = getParentFolder();
      if (!parent.children) parent.children = [];
      parent.children.push(draggedAsset);
      draggedAsset = null;
      currentFolder = currentFolder;
      assets = assets;
    }
    folderUpHover = false;
    folderUpDragCount = 0;
  }
  function getParentFolder() {
    if (currentPath.length === 0) return { children: assets };
    let path = currentPath.slice(0, -1);
    let folder = { children: assets };
    for (const fid of path) {
      folder = folder.children.find(c => c.id === fid);
    }
    return folder;
  }

  /* Select Mode: Automatisch aktiv, wenn select_filter_callback definiert ist */
  function toggleSelect(asset) {
    if (inline) {
      // Im Inline-Modus kein Toggle, direkt Select-Event auslsen
      dispatch('select', { asset });
      if (on_select_callback) {
        on_select_callback(asset);
      }
    } else {
      // Im normalen Modus wie bisher togglen
      asset.selected = !asset.selected;
      if (asset.selected) {
        dispatch('select', { asset });
        if (on_select_callback) {
          on_select_callback(asset);
        }
      }
      currentFolder = currentFolder;
      assets = assets;
    }
  }
  function thumbnailMouseMove(event,asset) {
    if (!asset.thumbnailMouseMove) return
    asset.thumbnailMouseMove(event)
  }
</script>

<style>
  /* Modus-Schalter fr Normal/Re-Order und Project/All */
  .mode-switch {
    display: flex;
    justify-content: space-between;
    padding: 8px 16px;
    border-bottom: 1px solid #ccc;
  }
  .mode-group {
    display: flex;
    gap: 8px;
  }
  .mode-switch button {
    padding: 6px 12px;
    border: 1px solid #ccc;
    background-color: #2f2e2e;
    color: white;
    cursor: pointer;
    border-radius: 4px;
  }
  .mode-switch button.active {
    background-color: #ddb74f;
    color: black;
    font-weight: bold;
  }
  .repeater {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 16px;
    padding: 16px;
  }
  .repeater-item {
    position: relative;
    width: 200px;
    height: 112px;
    border-radius: 4px;
    overflow: hidden;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    cursor: grab;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2f2e2e;
  }
  .repeater-item:active {
    cursor: grabbing;
  }
  /* Folder-Up und Plus-Item: Gleiche Mae wie Assets, Inhalt zentriert */
  .folder-up,
  .plus-item {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .folder-up svg {
    width: 30px;
    height: 30px;
    fill: #555;
  }
  .plus-item svg {
    width: 50px;
    stroke: white;

  }
  /* Nur Asset-Items erhalten die Klasse "draggable-asset" fr Re-Order */
  .draggable-asset {
    /* dient nur als Selektor */
  }
  /* WICHTIG: In der Each-Schleife wird data-id gesetzt, damit die neue Sortierung ermittelt werden kann */
  /* Drop-Zone-Highlight */
  .drop-target {
    background-color: rgba(76, 175, 80, 0.2);
    outline: 2px dashed #4CAF50;
  }
  :global(.sortable-ghost) {
    opacity: 0.4;
    box-shadow: inset 0 0 0 2px #4CAF50;
    transform: scale(0.95);
  }
  :global(.sortable-drag) {
    box-shadow: 0 5px 15px rgba(0,0,0,0.15) !important;
    transform: scale(1.05) !important;
    cursor: grabbing !important;
  }
  .image-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .image {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 100%;
    max-height: 100%;
  }
  .title {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
 /*   background: linear-gradient(to bottom, rgba(0, 0, 0, 0.9), transparent);*/
    color: white;
    padding: 8px;
    font-size: 16px;
    z-index: 2;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
  }
  .title:hover {
    color: #ddb74f;
    transition: 0.6s;
  }
  .settings {
    position: absolute;
    top: 4px;
    right: 4px;
    z-index: 3;
    opacity: 0;
    transition: opacity 0.2s ease-in;
  }
  .repeater-item:hover .settings {
    opacity: 1;
  }
  /* Select Button */
  .select-btn {
    position: absolute;
    top: 4px;
    left: 4px;
    z-index: 4;
    background-color: rgba(0,0,0,0.5);
    border: none;
    color: white;
    padding: 4px;
    font-size: 20px;
    cursor: pointer;
    opacity: 0;
  }
  .select-btn:hover {
    color: #ddb74f;
  }
  .repeater-item:hover .select-btn {
    opacity: 1;
  }


  .selected {
    border: 2px solid #4CAF50;
  }
  .assets-container {
    min-height: 300px;
    width: 100%;
  }
  .drag-over-valid {
    background-color: rgba(76, 175, 80, 0.1);
    outline: 2px dashed #4CAF50;
  }
  .drag-over-invalid {
    background-color: rgba(244, 67, 54, 0.1);
    outline: 2px dashed #F44336;
  }
  .no-drag:hover {
    background-color: #ddb74f;
    transition: 0.6s;
    cursor: pointer;
  }
  .formInput {
    width: 100%;
    outline: 0;
    border: 0;
    border-bottom: 2px solid rgba(255, 255, 255, 0.2);
    margin-bottom: 10px;
    padding: 5px;
    font-size: 14px;
    border-radius: 3px;
    color: rgba(255, 255, 255, 0.9);
    background: none;
    font-family: system-ui, 'Segoe UI', Roboto;
  }
  label, p {
    color: rgb(193, 193, 193);
  }
</style>

<div class="assets-container" 
  class:drag-over-valid={dragStatus === 'valid'} 
  class:drag-over-invalid={dragStatus === 'invalid'}
  on:dragover|preventDefault|stopPropagation={handleFileDragOver}
  on:dragleave|preventDefault|stopPropagation={handleFileDragLeave}
  on:drop|preventDefault|stopPropagation={handleFilesDrop}>
  
  <!-- Modus-Schalter fr Normal/Re-Order und Project/All -->
  <div class="mode-switch">
  
    <div class="mode-group">   
       {#if viewMode==="project"}   
      <button on:click={() => mode = 'normal'} class:active={mode === 'normal'}>Default</button>
      <button on:click={() => mode = 'reorder'} class:active={mode === 'reorder'}>Re-Order</button> 
       {/if}
    </div>
  
    <div class="mode-group">
      <button on:click={() => dispatch('viewModeChange', { mode: 'project' })} class:active={viewMode === 'project'}>Project</button>
      <button on:click={() => dispatch('viewModeChange', { mode: 'all' })} class:active={viewMode === 'all'}>All</button>
      <slot name="loading-indicator"></slot>
    </div>
  </div>
  
  <!-- Alle Elemente als Grid-Items in derselben .repeater -->
  <div class="repeater" bind:this={repeaterElement}>
    {#if currentPath.length > 0}
      <div class="repeater-item folder-up no-drag"
           on:dragover|preventDefault={handleFolderUpDragOver}
           on:dragenter={(e) => handleFolderUpDragEnter(e)}
           on:dragleave={(e) => handleFolderUpDragLeave(e)}
           on:drop={(e) => handleFolderUpDrop(e)}
           on:click={() => navigateUp()}
           class:drop-target={folderUpHover}>
        <svg viewBox="0 0 16 16">
          <path d="M8 3L3 8h10L8 3z"/>
        </svg>
      </div>
    {/if}
    <div class="repeater-item plus-item no-drag" 
         on:click|stopPropagation={(event) => { add(event) }}
         on:drop|preventDefault|stopPropagation={() => { /* Plus-Item fhrt keine Drop-Logik aus */ }}>
      <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 16 16" stroke="currentColor" stroke-width="0.1">
        <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
      </svg>
    </div>
    {#each currentFolder.children as asset (asset.id)}
      <div 
        data-id={asset.id}
        class="repeater-item draggable-asset {asset.selected ? 'selected' : ''}"
        class:drop-target={asset.isDropTarget}
        draggable={mode === 'normal'}
        on:dragstart={(e) => { if(mode==='normal') handleDragStart(e, asset) }}
        on:dragover={(e) => { if(mode==='normal') handleDragOverItem(e, asset) }}
        on:dragenter={(e) => { if(mode==='normal') handleDragEnter(e, asset) }}
        on:dragleave={(e) => { if(mode==='normal') handleDragLeave(e, asset) }}
        on:drop={(e) => { if(mode==='normal') handleDropItem(e, asset) }}
        on:dragend={(e) => { if(mode==='normal') handleDragEnd(e) }}>
        {#if select_filter_callback && select_filter_callback(asset)}
          <button class="select-btn" on:click|stopPropagation={() => toggleSelect(asset)}>
            {#if inline}
              {'+'}
            {:else}
              {asset.selected ? '' : 'Select'}
            {/if}
          </button>
        {/if}
        <fds-image-editor-button 
          icon="moreSettings" 
          style="--theme-btn-text-color:white" 
          on:click|stopPropagation={(event) => assetSettingsMenu(event, asset)} 
          class="settings" />          
        <div class="title" alt="{asset.description}" on:click|stopPropagation={() => { openAsset(asset) }}>
          {asset.name}
        </div>
        {#if asset.thumbnailHTML}
          <div class="image-container" on:mousemove={(e) => { thumbnailMouseMove(e,asset)}}>
            {@html asset.thumbnailHTML}
          </div>
        {/if}
        {#if asset.thumbnail}
          <div class="image-container" on:mousemove={(e) => { thumbnailMouseMove(e,asset)}}>
            <img 
              class="image"
              src={asset.thumbnail}
              alt={asset.name}
              style={getImageStyle(asset.meta)}
              draggable="false" />
          </div>
        {:else}
          {#if !asset.thumbnailHTML}
          <div>No Thumbnail</div>
          {/if}
        {/if}
      </div>
    {/each}
  </div>
</div>

{#if showPropertiesDialog}
  <fds-image-editor-dialog
    open={showPropertiesDialog}
    onclosed={() => showPropertiesDialog = false}
    nobackdrop={true}
    style="--dialog-bgcolor: black;">
    <div slot="modalcontent" class="properties-dialog">
      <p>Edit Properties</p>
      <div class="form-group">
        <label>ID (can't be changed):</label>
        <input type="text" value={selectedAsset.id} class="formInput" disabled>
      </div>      
      <div class="form-group">
        <label>Name:</label>
        <input type="text" bind:value={editName} class="formInput">
      </div>
      <div class="form-group">
        <label>Description:</label>
        <textarea bind:value={editDescription} class="formInput"></textarea>
      </div>
      <div class="dialog-actions">
        <fds-image-editor-button type="button" on:click={() => showPropertiesDialog = false}>
          Cancel
        </fds-image-editor-button>
        <fds-image-editor-button type="button" on:click={saveProperties} state="active">
          Save
        </fds-image-editor-button>
      </div>
    </div>
  </fds-image-editor-dialog>
{/if}