<svelte:options tag="fds-search-models-3d"></svelte:options>
<script>
    import swal from "sweetalert";

    let externalModels = [];
    let searchstring;
    import {onMount, createEventDispatcher} from 'svelte'
    import {get_current_component} from "svelte/internal"
    let host = get_current_component().$$.root.host
    export let open = false;

    export let onclosed = ()=>{};
    export let glbbinarydowloaded = ()=>{};
    let exModelsloaded = false;
    let glbloaded=false;
    let cursor = 0;

    let svelteDispatch = createEventDispatcher();
    const dispatch = (name, detail) => {
        svelteDispatch(name, detail)
        host.dispatchEvent && host.dispatchEvent(new CustomEvent(name, {detail}))
    }

    export let type=""

    async function openLink(url, width = 1200, height = 900) {
        if (window.uxpHost) {
            window.uxpHost.postMessage({
                key: 'openLink',
                valueurl: url,
                valuewidth: width,
                valueheight: height,
            })
        } else {
            window.open(url, '_blank')
        }
    }

    onMount(() => {
        if (type==="primitives") {
            externalModels=[
                {name:"Cube",thumb:"appdata/primitives/cube.png","uid":"cube"},
                {name:"Sphere",thumb:"appdata/primitives/sphere.png","uid":"sphere"},
                {name:"Cylinder",thumb:"appdata/primitives/cylinder.png","uid":"cylinder"},
                {name:"Cone",thumb:"appdata/primitives/cone.png","uid":"cone"},
                {name:"Torus",thumb:"appdata/primitives/torus.png","uid":"torus"}
            ]
        }
    });


    let selectRec = (el)=>{
        console.log("select record: ",el);
        downloadglb(el.uid,el.thumb);
    };

    const downloadglb = async (imageid,thumb) => {

        glbloaded = false
        let url
         if (type==="primitives") {
            let retval = await downloadglbbinaryfile({imageid,thumb});
            open = false;
            return retval;
         } else {
            url =  encodeURI(`https://www.flyingdog.de/sketchfabapi/download.php?imageid=${imageid}`);
         }

        console.log("open url::",url);
        try {
            let res = await fetch(url, {
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json",
                },
                method: 'get'
            });
            let reqresult =  await res.json();
            console.log(reqresult);

        } catch (error) {
            await swal("Server error", error.toString() + "\n" + url)
        }
        let res = await downloadglbbinaryfile({imageid,thumb});
        open = false;
        glbloaded = true;
    }


    const downloadglbbinaryfile = async ({imageid,thumb}) => {
        let url
        if (type==="primitives") {

                     url =  encodeURI(`appdata/primitives/primitive_${imageid}.glb`);
        } else {
            url = `https://www.flyingdog.de/sketchfabapi/files/${imageid}.glb`;
        }
        fetch(url)
            .then(response =>   response.arrayBuffer())
            .then(buffer => {
                glbbinarydowloaded(buffer,thumb,searchstring);
            })
            .catch(async err =>  {return await swal("Server error", err.toString() + "\n" + url)});
    }



    const search = async ({searchsuffix,reset}) => {
        exModelsloaded = false;
        let url =  encodeURI(`https://www.flyingdog.de/sketchfabapi/api.php?type=models&animated=false&sort_by=likeCount&archives_max_size=1500000&downloadable=true&archives_flavours=false&q=${searchstring}`);
        if (searchsuffix) url = url+searchsuffix;
        console.log("open url::",url);

        try {
            let res = await fetch(url, {
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json",
                },
                method: 'get'
            });
            let reqresult =  await res.json();
            if(reset){
                externalModels = reqresult;
                externalModels = externalModels;
            } else if(searchsuffix){
                externalModels = [...externalModels,...reqresult];
                externalModels = externalModels;
            }

            exModelsloaded = true;
        } catch (error) {
            await swal("Server error", error.toString() + "\n" + url)
        }
    }

    const scrollBottom = (ev) => {
        let el = ev.target;
        console.log("target:::",el);
        if( el.scrollTop > (el.scrollHeight - el.offsetHeight - 50)){
            loadNext();
        }
    }

    const loadNext  = async () => {
        if (!exModelsloaded) return;
        cursor = cursor+24;
        let searchsuffix = `&count=${cursor}&cursor=${cursor}`;
        search({searchsuffix});
    }





</script>


<div class="layers colorScheme">


    <fds-image-editor-dialog maxheight="1200px" width="1330px" {open} onscroll={(ev)=>{scrollBottom(ev)}}
                             onclosed={()=>{open=false; host.dispatchEvent(new CustomEvent("close", {  })); onclosed();}}>
                        <span slot="modalcontent">

                            {#if type==="primitives"}

                           <div class="repeater">
                               {#each externalModels as model, i}
                                       <!-- svelte-ignore a11y-click-events-have-key-events -->
                                       <div class="repeater-item">
                                           <div on:click={selectRec(model)} class="title">{model.name}</div>
                                           <img on:click={selectRec(model)} src={model.thumb} class="image_primitive" alt={model.name}>
                                      </div>
                              {/each}
                          </div>
                            {:else}
                            <div class="searchline">
                                <input lang="en" type="text" placeholder="Search text" class="formInput"
                                       bind:value={searchstring}>
                               <button on:click={()=>{search({reset:true})}} variant="cta" class="formInput ok">Search</button>
                       </div>

                           <div class="repeater">
                               {#each externalModels as model, i}
                                       <!-- svelte-ignore a11y-click-events-have-key-events -->
                                       <div class="repeater-item">
                                           <div on:click={selectRec(model)} class="title">{model.name}</div>
                                           <img on:click={selectRec(model)} src={model.thumb} class="image" alt={model.name}>
                                           <div class="Models">
                                               <!--- <p class="Modelstext">{@html modifier.name}</p> --->
                                               <p class="Modelsexample_prompt">license: {model.license}</p>
                                               <a target="_blank" class="modellink" on:click={()=>{openLink(model.modelpagelink)}} href="{model.modelpagelink}"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-link" viewBox="0 0 16 16"> <path d="M6.354 5.5H4a3 3 0 0 0 0 6h3a3 3 0 0 0 2.83-4H9c-.086 0-.17.01-.25.031A2 2 0 0 1 7 10.5H4a2 2 0 1 1 0-4h1.535c.218-.376.495-.714.82-1z"/> <path d="M9 5.5a3 3 0 0 0-2.83 4h1.098A2 2 0 0 1 9 6.5h3a2 2 0 1 1 0 4h-1.535a4.02 4.02 0 0 1-.82 1H12a3 3 0 1 0 0-6H9z"/> </svg></a>

                                          </div>

                                      </div>
                              {/each}
                          </div>
                            {/if}
                        



                       </span>
   </fds-image-editor-dialog>
</div>

<style>
   .searchline{
       display: flex;
       width: 300px;
   }



   .ok {
       cursor:pointer;
       border: 0;
       background-color: rgba(255,255,255,0.2);

   }
   .ok:hover {
       background-position: 100% 0;
       transition: all .4s ease-in-out;
       box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
       background-color:   var(--btn-selected-bgcolor);
       color: var(--btn-selected-text-color);
   }

   .formInput {
       width: 100%;
       outline: 0;
       border: 0;
       border-bottom: 2px solid rgba(255, 255, 255, 0.2);

       padding: 5px;
       font-size: 14px;
       font-weight: normal;
       border-radius: 3px;
       color: rgba(255, 255, 255, 0.9);
       margin-bottom: 10px;
       display: block;
       font-family: system-ui, "Segoe UI", Roboto;
   }

   .formInput::placeholder {
       font-weight: normal;
       opacity: 0.5;
       color: rgba(255, 255, 255, 0.7);
   }

   .title {
       width: 100%;
       overflow: hidden;
   }

   .colorScheme {
       --stage-color: var( --theme-stage-color);
       --btn-selected-bgcolor: var( --theme-btn-selected-bgcolor);
       --btn-selected2-bgcolor: var( --theme-btn-selected2-bgcolor);
       --btn-selected-border-color: var( --theme-btn-selected-border-color);
       --btn-text-color: var( --theme-btn-text-color);
       --btn-selected-text-color: var( --theme-btn-selected-text-color);

       --bgcolor: var( --theme-bgcolor);
       --text-color: var( --theme-text-color);
       /* Slider */
       --slider-track-bgcolor:       var(--theme-slider-track-bgcolor);
       --slider-track-focus-bgcolor: var(--theme-slider-track-focus-bgcolor);
       --slider-track-border-color:        var(--theme-slider-track-border-color);
       --slider-thumb-color:        var(--theme-slider-thumb-color);

       --btn-bw-simple-bgcolor: #6b6b6b;
       --btn-bw-simple-bgcolor-hover: #e3ce74;
       --btn-bw-simple-color: white;
       --btn-bw-simple-color-hover: #000000;

   }

   * {
       box-sizing: border-box;
   }







   .repeater {
       display: flex;
       flex-direction: row;
       flex-wrap: wrap;
       width: 100%;
   }

   .repeater-item {
       padding: 10px;
       display: flex;
       flex-direction: column;
       /*color: black;*/



       cursor: pointer;
   }

   .repeater-item .modellink{
       color: #a19f9f;
   }
   .repeater-item:hover {
       background-color: var(--btn-selected-bgcolor) ;
       color: var(--btn-selected-text-color);
       transition: all .4s ease-in-out;
   }
   .repeater-item .title {
       margin-top: 10px;
       margin-bottom: 0px;
   }

   .repeater-item .image {
       max-width:250px;
       max-height:144px;
       width: auto;
       height: auto;
   }
   .repeater-item .image_primitive {
       max-width:250px;
       max-height:250px;
       width: auto;
       height: auto;
   }
   
   @media (prefers-color-scheme: dark), (prefers-color-scheme: darkest) {
       label {
           color: #E0E0E0;
       }
   }






   .Modelsexample_prompt {
       font-style: italic;
       width: 215px;
       font-size: 10px;
       margin:0;
   }

   .Models{
       display: flex;
   }

</style>
