<svelte:options customElement="fds-test-asset-plugin"></svelte:options>

<script>
  import { onMount } from 'svelte';

  export let ready = new Promise((resolve) => {
    onMount(() => resolve(true));
  });

  async function generateThumbnail(src, naturalWidth,naturalHeight,targetSize=200) {    
        const scale = Math.min(
          targetSize / naturalWidth,
          targetSize / naturalHeight
        )        
        const width = naturalWidth * scale;
        const height = naturalHeight * scale;        
        return await globalThis.gyre.imageAPI.scaleImageCanvas(src,width,height,{format:"image/jpeg",quality:0.8,color:"#ffffff"})
  }

  // from file to base64 (it is not an URL)
  async function fileToBase64(file) {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = (e) => resolve(e.target.result);
      reader.readAsDataURL(file);
    });
  }
  export async function testMethod(asset,currentFolder) {
    alert("test")
  }
  export async function deleteAsset(asset) {
    console.log("delete ",asset)
  }
  export async function cloneAsset(asset,currentFolder) {
    let cloneAsset=JSON.parse(JSON.stringify(asset))
    cloneAsset.id=crypto.randomUUID()
    cloneAsset.name="Copy of "+cloneAsset.name
    currentFolder.children.push(cloneAsset)
  }

  export async function importFileAsAsset(file, currentFolder) {
    if (file.name.endsWith('.gyre')) {
      console.warn('.gyre-Dateien werden separat verarbeitet');
      return;
    }

    const base64 = await fileToBase64(file);
    const { width, height } = await globalThis.gyre.imageAPI.getImageSize(base64);
    
    // Generiere Thumbnail
    const thumbnail = await generateThumbnail(base64,width,height);
    let layer={
          type: "image",
          url: base64,
          width,
          height,
          rotation: 0,
          visible: true
    }
    let name=file.name.replace(/\.[^/.]+$/, "")
    const newAsset = {
      name: name,
      id: crypto.randomUUID(),
      type: "fds-test-asset-plugin",   
      thumbnail,
      meta: { 
        width, 
        height
      },
      ref: {
        image: base64
      },
      data: {
        layers: [layer]
      }
    };

    currentFolder.children.push(newAsset)
    return newAsset
  }

  export async function createEmptyAsset(currentFolder) {    
    let width=512
    let height=512    
    let base64='data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNkYAAAAAYAAjCB0C8AAAAASUVORK5CYII='
    // Generiere Thumbnail
    const thumbnail = await generateThumbnail(base64,width,height);
    let layer={
          type: "image",
          url: base64,
          width,
          height,
          rotation: 0,
          visible: true
    }
    const newAsset = {
      name: "New Image",
      id: crypto.randomUUID(),
      type: "fds-test-asset-plugin",   
      thumbnail,
      meta: { 
        width, 
        height
      },
      ref: {
        image: base64
      },
      data: {
        layers: [layer]
      }
    };

     currentFolder.children.push(newAsset)
     return newAsset
  }

</script>