<svelte:options customElement="fds-assets"></svelte:options>

<script>
  import { createEventDispatcher } from 'svelte';
  import AssetsInline from './AssetsInline.svelte';

  const dispatch = createEventDispatcher();

  // Props
  export let assets = [];
  export let select_filter_callback = null;
  export let on_select_callback = null;
  export let inline = false;
  export let open = false;
  
  // For assets view selection (Project/All)
  let viewMode = 'project';
  let serverAssets = [];
  let isLoading = false;
  
  // Function to fetch all assets from server
  async function fetchAllAssets() {
    isLoading = true;
    try {
      let url=globalThis.gyre.assetManager.serverURL + "/api/assets/all"
      const response = await fetch(url)
      if (!response.ok) {
        throw new Error('Failed to fetch assets from server');
      }
      const data = await response.json();
      serverAssets = Array.isArray(data) ? data : (data.assets || [])
    } catch (error) {
      console.error('Error fetching server assets:', error);
      serverAssets = [];
    } finally {
      isLoading = false;
    }
  }
  
  // Reactive variable for which assets to display
  $: displayedAssets = viewMode === 'project' ? assets : (serverAssets || []);
</script>

<style>
  .asset-wrapper {
    width: 100%;
    height: 100%;
  }
  
  .view-mode-switch {
    display: flex;
    gap: 8px;
    padding: 8px 16px;
    border-bottom: 1px solid #ccc;
    align-items: center;
  }
  
  .view-mode-switch button {
    padding: 6px 12px;
    border: 1px solid #ccc;
    background-color: #2f2e2e;
    color: white;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .view-mode-switch button.active {
    background-color: #ddb74f;
    color: black;
    font-weight: bold;
  }
  
  .loading-indicator {
    margin-left: 10px;
    color: white;
    font-size: 14px;
    animation: pulse 1.5s infinite;
  }
  
  @keyframes pulse {
    0% { opacity: 0.6; }
    50% { opacity: 1; }
    100% { opacity: 0.6; }
  }
</style>

{#if inline}
  <div class="asset-wrapper">
    <fds-assets-inline 
      assets={displayedAssets} 
      {select_filter_callback} 
      {on_select_callback} 
      {inline}
      viewMode={viewMode}
      on:select={(e) => dispatch('select', e.detail)}
      on:viewModeChange={(e) => {
        viewMode = e.detail.mode;
        if (viewMode === 'all' && serverAssets.length === 0) {
          fetchAllAssets();
        }
      }}>
      {#if isLoading}
        <span slot="loading-indicator" class="loading-indicator">Loading...</span>
      {/if}
    </fds-assets-inline>
  </div>
{:else}
  <!-- Button zum ffnen des Asset-Dialogs -->
  <fds-image-editor-button type="button" on:click={() => { open = true }} state="active">
    Assets
  </fds-image-editor-button>

  {#if open}
    <fds-image-editor-dialog 
      {open}
      fullscreenmode={true}
      usepopover={true}
      onclosed={() => { open = false; dispatch('close', {}); }}>
      <span slot="modalcontent">
        <fds-assets-inline 
          assets={displayedAssets} 
          {select_filter_callback} 
          {on_select_callback}
          inline={false}
          viewMode={viewMode}
          on:select={(e) => dispatch('select', e.detail)}
          on:viewModeChange={(e) => {
            viewMode = e.detail.mode;
            if (viewMode === 'all' && serverAssets.length === 0) {
              fetchAllAssets();
            }
          }}>
          {#if isLoading}
            <span slot="loading-indicator" class="loading-indicator">Loading...</span>
          {/if}
        </fds-assets-inline>
      </span>
    </fds-image-editor-dialog>
  {/if}
{/if}
