<svelte:options customElement="fds-presets-dialog-new"></svelte:options>
<script>
    import {get_current_component} from "svelte/internal"
    import {onMount, tick} from "svelte";
    // import swal from "sweetalert";
    let host = get_current_component().$$.root.host


    export let formElements
    export let metadata
    let currentMetadata={}
    export let preset_name
    export let general_config
    let customPresets=[]
    let workflowId
    let defaultPresets=[]
    let typesIgnore=["text_output","layer_image","drop_layers","magnifier","advanced_options","file"]
    let namesIgnore=["preview","newLayer","num_images","seed","prompt"]
    async function fetchPresets(url) {
        let presetJSON=await fetch(url)
        if (presetJSON) {
            defaultPresets=JSON.parse(presetJSON)
        }
    }
    $:  {
        // get workflow presets from workflow, other workflow or just an URL
        // workflowId can change here!

        if (metadata){
            currentMetadata=metadata
            if(!workflowId) workflowId=metadata.workflowid;
            general_config=globalThis.gyre.paletteValues.Config.generalConfig
            defaultPresets=[]
            if (metadata.presetType==="workflow") {

                workflowId=metadata.presetWorkflowId
                let otherMetadata=globalThis.gyre.ComfyUI.getGyreInfo(workflowId)
                currentMetadata=otherMetadata
                if (!otherMetadata) workflowId=null
                if (otherMetadata && otherMetadata.presets) defaultPresets=otherMetadata.presets
            } else {

                workflowId=metadata.workflowid
                if (metadata.presets) defaultPresets=metadata.presets
            }
            // get custom presets directly from general_config
            if (general_config && general_config.customPresets && workflowId && general_config.customPresets[workflowId]) {
                customPresets=general_config.customPresets[workflowId]
            }

            if (metadata.presetURL) {
                fetchPresets(metadata.presetURL)
            }
        }
        if (preset_name) {
            realPresetName=getRealPresetName()
        }

    }
    function postMessage(sendobj) {
        if (window.uxpHost) {
            window.uxpHost.postMessage(sendobj)
        } else if (window.postMessageAdapter) {
            window.postMessageAdapter.postMessage(sendobj)
        }
    }
    async function convertImageToBase64WebP(imageUrl) {
        return new Promise((resolve, reject) => {
            const img = new Image()
            img.onload = function() {
                const canvas = document.createElement('canvas')
                const ctx = canvas.getContext('2d')
                canvas.width = 250
                canvas.height = 250
                ctx.drawImage(img, 0, 0, 250, 250)
                const base64WebP = canvas.toDataURL('image/webp')
                resolve(base64WebP)
            };

            img.onerror = function() {
                reject(new Error("Error loading image."))
            }

            // Start loading the image
            img.src = imageUrl
        })
    }
    async function postMessageAction(postObj,keyToListen,keepListening){
        return new Promise((resolve) => {
            let func = async (event) => {
                let data = event.data;
                if (keyToListen && data.key === keyToListen ){
                    data.postMessageCallback = func;
                    resolve(data);
                    if(!keepListening) window.removeEventListener("message",func);
                }
            }

            if(!keyToListen){
                postMessage(postObj);
                resolve();
            } else{
                window.addEventListener("message",func);
                postMessage(postObj);
            }
        });
    }
    function updateFormElements(data) {

        for(let i=0;i<formElements.length;i++) {
            let element=formElements[i]
            if (data[element.name] !== null || data[element.name] !== undefined)  element.elementvalue=data[element.name]
        }

        //formElements.push({'name':Math.random(),'elementvalue':preset_name});
        formElements=formElements;
        //preset_name=""
        console.log("update now with data",data)
        host.dispatchEvent(new CustomEvent("update", { detail:{data:data}}))

    }
    async function saveCustomPresets() {
        if (!general_config) return
        if (!general_config.customPresets) general_config.customPresets={}
        general_config.customPresets[workflowId]=customPresets
        postMessageAction({
            key: 'changeGeneralConfig',
            value: general_config.customPresets,
            type: 'customPresets',
            skiprefresh: true
        })
    }



    onMount(() => {

    })

    let mode=""
    let newPresetName=""



    function selectPreset(e,name=null) {
        if (name) preset_name=name
        if (!preset_name || !preset_name.length) return
        if (preset_name==="ADD") {
            mode="add"
            newPresetName=""
            preset_name=""
        }
        realPresetName=getRealPresetName()
        if (preset_name.indexOf("custom_")>=0) {
            let idx=parseInt(preset_name.replace("custom_",""))
            let preset=customPresets[idx]
            let data=preset.data
            updateFormElements(data)
            open=false
        }
        if (preset_name.indexOf("default_")>=0) {
            let idx=parseInt(preset_name.replace("default_",""))
            let preset=defaultPresets[idx]
            let data=preset.data
            updateFormElements(data)
            open=false
        }
    }
    async function  preparePresetForAdd() {
        let preset={name:newPresetName,data:{} }
        for(let i=0;i<formElements.length;i++) {
            let element=formElements[i]
            if (!element.preset_ignore && !namesIgnore.includes(element.name) && !typesIgnore.includes(element.type)) {
                preset.data[element.name]=element.elementvalue
            }
            mode=""
        }
        if ((currentMetadata.presetType==="fullscreen" || currentMetadata.presetType==="URL") && withImage && globalThis.gyre.paletteValues.selectedLayer) {
            preset.image=await convertImageToBase64WebP(globalThis.gyre.paletteValues.selectedLayer.url)
        }
        return preset
    }
    async function addPreset() {
        if (!newPresetName.length) return
        await preparePresetForAdd()
        if (!customPresets) customPresets=[]
        customPresets.push(await preparePresetForAdd())
        saveCustomPresets()
    }

    async function addPresetToServer() {
        if (!newPresetName.length) return
        if (!currentMetadata.presets) currentMetadata.presets=[]
        currentMetadata.presets.push(await preparePresetForAdd())
        defaultPresets=currentMetadata.presets
        globalThis.gyre.ComfyUI.setGyreInfo(workflowId,currentMetadata)
    }
    function deletePreset(e,name=null) {
        if (!confirm("Are you sure?")) return
        if (name) preset_name=name
        if (preset_name.indexOf("custom_")>=0) {
            let idx=parseInt(preset_name.replace("custom_",""))
            customPresets.splice(idx,1)
            saveCustomPresets()
        }
        if (preset_name.indexOf("default_")>=0) {
            let idx=parseInt(preset_name.replace("default_",""))
            currentMetadata.presets.splice(idx,1)
            globalThis.gyre.ComfyUI.setGyreInfo(workflowId,currentMetadata)
        }       
        preset_name=""
    }

    function cancelAdd() {
        mode=""
    }
    let realPresetName=""
    function getRealPresetName() {
        if (!preset_name) return
        if (preset_name.indexOf("custom_")>=0) {
            let idx=parseInt(preset_name.replace("custom_",""))
            let preset=customPresets[idx]
            return preset.name
        }
        if (preset_name.indexOf("default_")>=0) {
            let idx=parseInt(preset_name.replace("default_",""))
            let preset=defaultPresets[idx]
            return preset.name
        }
    }
    let open=false
    let withImage=true
</script>
<style>
    .input {
        background-color: black;
        color: white;
        font-family: system-ui, -apple-system, "Segoe UI", Roboto, Ubuntu, Cantarell, "Noto Sans", sans-serif, "Segoe UI", Helvetica, Arial;
        padding: 3px;
        border: 1px solid white;
    }
    .newPreset {
        border: 1px solid rgb(227, 206, 116);
        border-radius: 5px;
        padding: 10px;
        margin: 10px;
    }
    label {
        display:inline-block;
        width:80px;
    }
    .repeater{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
    }

    .fullScreen {
        background-color: white;
        min-height:  calc(100vh - 25px);
    }
    .repeater-item {
        width: 300px;
        height: 300px;
        display: flex;
        flex-direction: column;
        color: black;
        cursor: pointer;
        position: relative;
    }

    .repeater-item .title {
        margin: 20px auto 0;
        font-weight: 400;
        font-size: 1.25rem;
    }

    .repeater-item .image {
        width: 215px;
        height: 215px;
        margin: 0 auto 0;
    }
    .repeater-item .add {
        position: absolute;
        left: calc( 50% - 15px);
        top: calc( 50% - 15px);
    }
    .repeater .center {
        justify-content: center;
    }
    .repeater-item:hover {
        background-color: #ddb74f;
        transition: all .2s ease-in;
    }
    .repeater-item:hover .delete {
        transition: all .2s ease-in;
        opacity: 1.0;
    }
    .repeater .newPreset {
        background-color: black;
        border: 0;
        margin-top: 45px;
        height: 200px;
    }
    .formLine {
        margin-bottom: 2ch;
    }

    .toggle {
        vertical-align: -5px;
        margin-right: 10px;
    }
    .repeater-item .delete {
        position: absolute;
        right: 15px;
        top: 15px;
        opacity: 0.0;
    }
</style>
{#if currentMetadata && currentMetadata.presetType && currentMetadata.presetType!=="none" && workflowId}

    {#if  currentMetadata.presetType==="dropdown"}
        {#if mode==="add"}
            <div class="newPreset">

                <p>Add Preset</p>
                <label for="name">Name:</label>
                <input type="text" name="name" bind:value={newPresetName} class="input">
                <p >
                    <label for="buttons"> </label>
                    <!-- svelte-ignore a11y-no-static-element-interactions -->
                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                    <fds-image-editor-button type="button" state="active" on:click={addPreset}>Add</fds-image-editor-button>
                    {#if !currentMetadata.tags.includes("Default")}
                        <!-- svelte-ignore a11y-no-static-element-interactions -->
                        <!-- svelte-ignore a11y-click-events-have-key-events -->
                        <fds-image-editor-button type="button" on:click={addPresetToServer}>Add To Server</fds-image-editor-button>
                    {/if}
                    <!-- svelte-ignore a11y-no-static-element-interactions -->
                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                    <fds-image-editor-button type="button" on:click={cancelAdd}>Cancel</fds-image-editor-button>
                </p>
            </div>
        {/if}
        {#if mode!=="add"}
            <select bind:value={preset_name} on:change={selectPreset} class="input">
                <option value="" >Custom</option>
                {#if defaultPresets}
                {#each defaultPresets as presetel,i}
                    <option value="default_{i}">{presetel.name}</option>
                {/each}                
                    {#if defaultPresets.length}
                        <option disabled></option>
                    {/if}
                {/if}
                {#if customPresets}
                    {#each customPresets as presetel,i}
                        <option value="custom_{i}">{presetel.name}</option>
                    {/each}
                {/if}
                <option disabled></option>
                <option value="ADD">Add Preset...</option>

            </select>
            {#if preset_name && preset_name.indexOf("custom_")>=0 }
                <!-- svelte-ignore a11y-no-static-element-interactions -->
                <!-- svelte-ignore a11y-click-events-have-key-events -->
                <fds-image-editor-button type="button" on:click={deletePreset}>Delete</fds-image-editor-button>
            {/if}
        {/if}
    {/if}
    {#if  currentMetadata.presetType==="fullscreen" || currentMetadata.presetType==="URL"}
        <!-- svelte-ignore a11y-no-static-element-interactions -->
        <!-- svelte-ignore a11y-click-events-have-key-events -->
        <fds-image-editor-button type="button" on:click={()=>{open=true}}>{#if !preset_name || !preset_name.length}
            Select...
        {:else}
            {realPresetName}
        {/if}

        </fds-image-editor-button>


        {#if open}
            <fds-image-editor-dialog {open} fullscreenmode={true} usepopover={true} onclosed={()=>{open=false; }}>

       <span slot="modalcontent">
            <div class="fullScreen">
            <div class="repeater">
                {#if defaultPresets}
                {#each defaultPresets as presetel,i}

                        <!-- svelte-ignore a11y-no-static-element-interactions -->
                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                        <div on:click={(e)=> { selectPreset(e,"default_"+i)}} class="repeater-item" class:center={!presetel.image}>
                            {#if !currentMetadata.tags.includes("Default") && currentMetadata.presetType!=="URL"}
                                <fds-image-editor-button icon="delete" style="--theme-btn-text-color:red"  on:click={(e) => { deletePreset(e,"default_"+i) }} class="delete"></fds-image-editor-button>
                            {/if}
                            <div class="title" >{presetel.name}</div>
                            {#if presetel.image}
                                <img class="image" src={presetel.image} alt={presetel.name}>
                            {/if}
                        </div>
                {/each}
                {/if}
            </div>           
                <hr>
                <div class="repeater">
                    {#if customPresets}
                {#each customPresets as presetel,i}

                        <!-- svelte-ignore a11y-no-static-element-interactions -->
                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                        <div on:click={(e)=> { selectPreset(e,"custom_"+i)}} class="repeater-item" class:center={!presetel.image}>
                            <fds-image-editor-button icon="delete" style="--theme-btn-text-color:red"  on:click={(e) => { deletePreset(e,"custom_"+i) }} class="delete"></fds-image-editor-button>
                            <div class="title" >{presetel.name}</div>
                            {#if presetel.image}
                                <img class="image" src={presetel.image} alt={presetel.name}>
                            {/if}
                        </div>
                {/each}
                {/if}
                    {#if mode!=="add"}
                    <!-- svelte-ignore a11y-click-events-have-key-events -->
                        <!-- svelte-ignore a11y-no-static-element-interactions -->
                    <div on:click={(e)=> { selectPreset(e,"ADD")}} class="repeater-item">
                        <svg class="add" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 18 18"  style="width: 30px; height: 30px;"><title></title><g style=""><rect id="Canvas" fill="#ff13dc" opacity="0" width="18" height="18"></rect><path class="fill" d="M14.5,8H10V3.5A.5.5,0,0,0,9.5,3h-1a.5.5,0,0,0-.5.5V8H3.5a.5.5,0,0,0-.5.5v1a.5.5,0,0,0,.5.5H8v4.5a.5.5,0,0,0,.5.5h1a.5.5,0,0,0,.5-.5V10h4.5a.5.5,0,0,0,.5-.5v-1A.5.5,0,0,0,14.5,8Z"></path></g></svg>
                    </div>
                    {/if}
                    {#if mode==="add"}
                    <div class="newPreset">
                        <p>Add Preset</p>
                        <div class="formLine">
                            <label for="name">Name:</label><input type="text" name="name" bind:value={newPresetName} class="input">
                        </div>
                        {#if globalThis.gyre.paletteValues.selectedLayer && globalThis.gyre.paletteValues.selectedLayer.url }
                            <div class="formLine">
                                <label for="example">Example:</label>
                                <fds-image-editor-toggle name="example"  value={withImage} on:change={() => { withImage=!withImage}} class="toggle"></fds-image-editor-toggle>Use Image from current Layer
                            </div>
                        {/if}
                        <p >
                            <label for="buttons"> </label>
                            <!-- svelte-ignore a11y-no-static-element-interactions -->
                            <!-- svelte-ignore a11y-click-events-have-key-events -->
                            <fds-image-editor-button type="button" state="active" on:click={addPreset}>Add</fds-image-editor-button>
                            {#if !currentMetadata.tags.includes("Default") && currentMetadata.presetType!=="URL"}
                            <!-- svelte-ignore a11y-no-static-element-interactions -->
                                <!-- svelte-ignore a11y-click-events-have-key-events -->
                            <fds-image-editor-button type="button" on:click={addPresetToServer}>Add To Server</fds-image-editor-button>                
                            {/if}
                            <!-- svelte-ignore a11y-no-static-element-interactions -->
                            <!-- svelte-ignore a11y-click-events-have-key-events -->
                            <fds-image-editor-button type="button" on:click={cancelAdd}>Cancel</fds-image-editor-button>
                        </p>
                    </div>
                    {/if}
            </div>
        </div>
        </span>
            </fds-image-editor-dialog>
        {/if}
    {/if}

{/if}

