<svelte:options tag="fds-posenet-select"></svelte:options>
<script>
    import swal from "sweetalert";
    let type;
    let overviewel;
    let externalModifiers = [];
    let searchstring;
    import {onMount, createEventDispatcher, beforeUpdate} from 'svelte'
    import {get_current_component} from "svelte/internal"
    let host = get_current_component().$$.root.host
    export let open = false;
    export let initwidth=512;
    export let initheight=512;
    export let serverurl="";
    export let onclosed = ()=>{};
    export let posedowloaded = ()=>{};
    let exmodifiersloaded = false;
    let glbloaded=false;
    let cursor = 0;

    let svelteDispatch = createEventDispatcher();
    const dispatch = (name, detail) => {
        svelteDispatch(name, detail)
        host.dispatchEvent && host.dispatchEvent(new CustomEvent(name, {detail}))
    }



    onMount(() => {
        load();
    });


    let selectRec = (el)=>{
        console.log("select record: ",el);
        downloadpose({poseid:el.id});
    };

    const downloadpose = async ({poseid}) => {
        glbloaded = false;
        let url =  encodeURI(`https://www.flyingdog.de/pose/jsons/${poseid}.json`);
        console.log("open url::",url);
        try {
            let res = await fetch(url, {
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json",
                },
                method: 'get'
            });
            let reqresult =  await res.json();
            reqresult.thumb=`https://www.flyingdog.de/pose/images/${poseid}.jpg`
            console.log(reqresult);
            posedowloaded(reqresult);
        } catch (error) {
            await swal("Server error", error.toString() + "\n" + url)
        }

        open = false;
        glbloaded = true;
    }




    const load = async () => {
        exmodifiersloaded = false;
        let url =  encodeURI(`https://www.flyingdog.de/pose/json/posesdata.json`);
        console.log("open url::",url);

        try {
            let res = await fetch(url, {
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json",
                },
                method: 'get'
            });
            let reqresult =  await res.json();
                externalModifiers = reqresult;
                externalModifiers = externalModifiers;
                exmodifiersloaded = true;
        } catch (error) {
            await swal("Server error", error.toString() + "\n" + url)
        }
    }







</script>


<div class="layers colorScheme">

    <fds-image-editor-dialog width="1330px" {open}
                             onclosed={()=>{open=false; host.dispatchEvent(new CustomEvent("close", {  })); onclosed();}}>
                        <span slot="modalcontent">


                            <div class="repeater">
                                {#if open}
                                    {#each externalModifiers as modifier, i}
                                            <!-- svelte-ignore a11y-click-events-have-key-events -->
                                            <div class="repeater-item">
                                                <div on:click={selectRec(modifier)} class="title">{modifier.file_name||modifier.id}</div>
                                                <img on:click={selectRec(modifier)} src={`https://www.flyingdog.de/pose/images/${modifier.id}.jpg`} class="image" alt={modifier.id}>
                                                <div class="modifiers">
                                                    <!--- <p class="modifierstext">{@html modifier.name}</p> --->
                                                    <!---<p class="modifiersexample_prompt">{modifier.id}</p> --->
                                               </div>

                                           </div>
                                   {/each}
