<svelte:options tag="fds-search-shapes"></svelte:options>
<script>
    import swal from "sweetalert";

    let externalImages = [];
    let searchstring;
    import {onMount, createEventDispatcher} from 'svelte'
    import {get_current_component} from "svelte/internal"

    let host = get_current_component().$$.root.host
    export let open = false;

    export let onclosed = () => {
    };
    export let shapedowloaded = () => {
    };
    let exModelsloaded = false;
    let imageloaded = false;
    let cursor = 0;
    let currentpage = 1;
    let allpages = 1;

    let svelteDispatch = createEventDispatcher();
    const dispatch = (name, detail) => {
        svelteDispatch(name, detail)
        host.dispatchEvent && host.dispatchEvent(new CustomEvent(name, {detail}))
    }

    export let type = ""


    onMount(() => {
        if (type === "primitives") {
            setPredefinedShapes();
        }
    });


    $: if(type === "primitives"){
        setPredefinedShapes();
    }

    $: if(type !== "primitives"){
        externalImages = [];
    }


    let setPredefinedShapes = () => {
        externalImages = [
            {title: "circle-o",image: "https://www.flyingdog.de/shape/images/new/circle-o.svg"},
            {title: "rectangle",image: "https://www.flyingdog.de/shape/images/new/rectangle.svg"},
            {title: "triangle-stroked-11",image: "https://www.flyingdog.de/shape/images/new/triangle-stroked-11.svg"},
            {title: "android-star-outline",image: "https://www.flyingdog.de/shape/images/new/android-star-outline.svg"},
            {title: "vector-line-curve",image: "https://www.flyingdog.de/shape/images/new/vector-line-curve.svg"},
            {title: "cross-15",image: "https://www.flyingdog.de/shape/images/new/cross-15.svg"},
            {title: "ios-moon-outline",image: "https://www.flyingdog.de/shape/images/new/ios-moon-outline.svg"}
        ]
    }



    let selectRec = (el) => {
        console.log("select record: ", el);
        downloadimage(el.title, el.image);
    };

    const imageUrlToBase64 = async (url) => {
        let data;
        try {
            data = await fetch(url);
        } catch (error) {
            await swal("Server error", error.toString() + "\n" + url)
        }
        const text = await data.text();
        return {svgtext:text,base64:"data:image/svg+xml;base64," + window.btoa(unescape(encodeURIComponent(text)))};
    };


    const downloadimage = async (imageid, thumb) => {

        imageloaded = false

        console.log("try to download shape");
        let url =  encodeURI(thumb);

        let {base64,svgtext} = await imageUrlToBase64(url);
        shapedowloaded(base64,svgtext);

        open = false;
        imageloaded = true;
    }





    const search = async ({searchsuffix, reset}) => {
        exModelsloaded = false;
        let url = encodeURI(`https://www.flyingdog.de/shape/search.php?search=${searchstring}`);
        if (searchsuffix) url = url + searchsuffix;
        console.log("open url::", url);
        try {
            let res = await fetch(url, {
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json",
                },
                method: 'get'
            });
            let reqresult = await res.json();
            reqresult = reqresult.items;
            allpages = reqresult.pages;
            if (reset) {
                externalImages = reqresult;
                externalImages = externalImages;
            } else if (searchsuffix) {
                externalImages = [...externalImages, ...reqresult];
                externalImages = externalImages;
            }

            exModelsloaded = true;
        } catch (error) {
            await swal("Server error", error.toString() + "\n" + url)
        }
    }

    const scrollBottom = (ev) => {
        let el = ev.target;
        console.log("target:::", el),"allpages",allpages,currentpage;
        if(currentpage>=allpages) return;
        if (el.scrollTop > (el.scrollHeight - el.offsetHeight - 50)) {
            loadNext();
        }
    }

    const loadNext = async () => {
        if (!exModelsloaded) return;
        cursor = cursor + 24;
        currentpage = currentpage+1;
        let searchsuffix = `&page=${currentpage}&cursor=${cursor}`;
        search({searchsuffix});
    }


</script>


<div class="layers colorScheme">


    <fds-image-editor-dialog maxheight="1200px" width="1330px" {open} onscroll={(ev)=>{scrollBottom(ev)}}
                             onclosed={()=>{open=false; host.dispatchEvent(new CustomEvent("close", {  })); onclosed();}}>
                        <span slot="modalcontent">

                            {#if type === "primitives"}
                                   <div class="repeater">
                                           {#each externalImages as model, i}
                                                   <!-- svelte-ignore a11y-click-events-have-key-events -->
                                                   <div class="repeater-item">
                                                       <div on:click={selectRec(model)} class="title">{model.title}</div>
                                                       <img on:click={selectRec(model)} src={model.image} class="image"
                                                            alt={model.title}>
                                                  </div>
                                          {/each}
                                  </div>
                            {:else}
                            <div class="searchline">
                                <input lang="en" type="text" placeholder="Search text" class="formInput"
                                       bind:value={searchstring}>
                               <button on:click={()=>{search({reset:true})}} variant="cta"
                                       class="formInput ok">Search</button>
                            </div>

                           <div class="repeater">
                               {#each externalImages as model, i}

                                       <!-- svelte-ignore a11y-click-events-have-key-events -->
                                       <div class="repeater-item">
                                           <div on:click={selectRec(model)} class="title">{model.title}</div>
                                           <img on:click={selectRec(model)} src={model.image} class="image"
                                                alt={model.name}>
                                           <div class="Models">
                                               <!--- <p class="Modelstext">{@html modifier.name}</p> --->
                                               <!--- <p class="Modelsexample_prompt">license: {model.license}</p> --->
                                          </div>

                                      </div>
                              {/each}
                          </div>
                            {/if}
                        



                       </span>
    </fds-image-editor-dialog>
</div>

