<svelte:options customElement="fds-asset-video"></svelte:options>

<script>
  import { onMount } from 'svelte';

  export let ready = new Promise((resolve) => {
    onMount(() => resolve(true));
  });

  export async function deleteAsset(asset) {
    await globalThis.gyre.assetManager.serverDeleteAsset(asset.id)
  }

  // Separated progress callback function
  function handleUploadProgress(newAsset, progress) {
    let gyre = globalThis.gyre;
    
    // Format progress to have at most 2 decimal places
    const formattedProgress = parseFloat(progress).toFixed(2);
    
    // Create or update progress animation
    if (!newAsset.thumbnailHTML || newAsset.thumbnailHTML.indexOf('upload-progress-container') === -1) {
      // First time - create the container and styles
      newAsset.thumbnailHTML = `
        <div class="upload-progress-container" style="
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          font-family: Arial, sans-serif;
          padding: 20px;
          border-radius: 8px;
          box-sizing: border-box;
        ">
          <div class="progress-text" style="
            font-size: 16px;
            font-weight: bold;
            margin-bottom: 10px;
            color: #333;
          ">Uploading...</div>
          <div class="progress-bar-container" style="
            width: 90%;
            height: 10px;
            border-radius: 5px;
            overflow: hidden;
            margin-bottom: 10px;
          ">
            <div class="progress-bar" style="
              height: 100%;
              width: ${formattedProgress}%;
              background: linear-gradient(90deg, #4a90e2, #63b3ed);
              border-radius: 5px;
              transition: width 0.3s ease;
            "></div>
          </div>
          <div class="progress-percentage" style="
            font-size: 14px;
            color: #555;
          ">${formattedProgress}%</div>
        </div>
      `;
    } else if (progress >= 100) {
      // Complete - show processing animation
      newAsset.thumbnailHTML = `
        <div class="upload-progress-container" style="
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          font-family: Arial, sans-serif;
          padding: 20px;
          border-radius: 8px;
          box-sizing: border-box;
        ">
          <div class="progress-text" style="
            font-size: 12px;
            font-weight: bold;
            margin-bottom: 10px;
            color: #ccccff;
          ">Processing...</div>
          <div class="processing-animation" style="
            width: 40px;
            height: 40px;
            margin: 10px 0;
            border: 3px solid rgba(0,0,0,0.1);
            border-color: #00c0ff;
            border-radius: 50%;
            border-top-color: #4a90e2;
            animation: spin 1s linear infinite;
          "></div>
          <style>
            @keyframes spin {
              to { transform: rotate(360deg); }
            }
          </style>
        </div>
      `;
    } else {
      // Update in progress - completely replace the HTML instead of trying to modify it
      newAsset.thumbnailHTML = `
        <div class="upload-progress-container" style="
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          font-family: Arial, sans-serif;
          background-color: #f5f5f5;
          padding: 20px;
          border-radius: 8px;
          box-sizing: border-box;
        ">
          <div class="progress-text" style="
            font-size: 16px;
            font-weight: bold;
            margin-bottom: 10px;
            color: #333;
          ">Uploading...</div>
          <div class="progress-bar-container" style="
            width: 90%;
            height: 10px;
            background-color: #e0e0e0;
            border-radius: 5px;
            overflow: hidden;
            margin-bottom: 10px;
          ">
            <div class="progress-bar" style="
              height: 100%;
              width: ${formattedProgress}%;
              background: linear-gradient(90deg, #4a90e2, #63b3ed);
              border-radius: 5px;
              transition: width 0.3s ease;
            "></div>
          </div>
          <div class="progress-percentage" style="
            font-size: 10px;
            color: #555;
          ">${formattedProgress}%</div>
        </div>
      `;
    }
    
    // Refresh UI
    gyre.assetManager.refresh();
  }

  // Separated result callback function
  function handleUploadResult(newAsset, result) {
    let gyre = globalThis.gyre;
    
    // Store metadata in the asset
    newAsset.meta = result.asset.meta;
    
    // Set default thumbnail
    newAsset.thumbnail = gyre.assetManager.serverURL + "/" + result.asset.meta.thumbnailPath;
    
    // Get video properties from metadata
    const duration = newAsset.meta.duration;
    const durationFrames = newAsset.meta.durationFrames;
    const proxyVideoUrl = gyre.assetManager.serverURL + "/" + result.asset.meta.proxyPath;
    
    // Store video element directly in the asset to maintain the reference
    newAsset.videoElement = document.createElement('video');
    newAsset.videoElement.src = proxyVideoUrl;
    newAsset.videoElement.crossOrigin = 'anonymous';
    newAsset.videoElement.muted = true;
    newAsset.videoElement.preload = 'auto';
    
    // Create canvas once and store on asset
    newAsset.canvas = document.createElement('canvas');
    // Set initial size - will be updated when video loads
    newAsset.canvas.width = 640;
    newAsset.canvas.height = 360;
    newAsset.ctx = newAsset.canvas.getContext('2d');
    
    // Track loading state - store in newAsset so it's accessible across calls
    newAsset.videoLoaded = false;
    
    // Initialize video
    newAsset.videoElement.addEventListener('loadeddata', () => {
      newAsset.videoLoaded = true;
      // Update canvas dimensions to match video
      newAsset.canvas.width = newAsset.videoElement.videoWidth;
      newAsset.canvas.height = newAsset.videoElement.videoHeight;
      console.log("Video loaded and ready for frame extraction");
    });
    
    newAsset.videoElement.addEventListener('error', (e) => {
      console.error("Error loading video:", e);
      newAsset.videoLoaded = false;
    });
    
    // Start loading the video
    newAsset.videoElement.load();
    
    // Extract frame at specific time position - store this function on the asset
    newAsset.extractFrameAt = (timePosition) => {
      return new Promise((resolve) => {
        // Define a timeout to prevent hanging if seeking doesn't complete
        let timeoutId = setTimeout(() => {
          console.log("Seek timeout, resolving null");
          resolve(null);
        }, 3000); // 3 second timeout
        
        if (!newAsset.videoLoaded || !newAsset.videoElement) {
          console.log("Video not loaded yet, cannot extract frame");
          clearTimeout(timeoutId);
          resolve(null);
          return;
        }
        
        try {
          // Set video time
          newAsset.videoElement.currentTime = timePosition;
          console.log("extractFrameAt", timePosition);
          
          // Use a one-time event listener for seeked
          const seekHandler = function() {
            try {
              clearTimeout(timeoutId); // Clear timeout when successful
              
              // Draw the current frame to canvas
              newAsset.ctx.drawImage(newAsset.videoElement, 0, 0, newAsset.canvas.width, newAsset.canvas.height);
              
              // Get frame as data URL
              const frameDataUrl = newAsset.canvas.toDataURL('image/jpeg');
              resolve(frameDataUrl);
            } catch (err) {
              console.error("Error drawing video frame:", err);
              resolve(null);
            } finally {
              // Remove this event listener
              newAsset.videoElement.removeEventListener('seeked', seekHandler);
            }
          };
          
          // Add the event listener
          newAsset.videoElement.addEventListener('seeked', seekHandler);
        } catch (err) {
          console.error("Error setting video time:", err);
          clearTimeout(timeoutId);
          resolve(null);
        }
      });
    };
    
    // Simplified mouse move handler - doesn't rely on currentTarget
    newAsset.thumbnailMouseMove = async (e) => {
      try {
        // Try to get a proper position from the event
        let position = 0;
        
        // First, try to use event target or currentTarget for the most accurate position
        if (e && (e.target || e.currentTarget)) {
          try {
            // Try target first
            let element = e.target;
            // Fall back to currentTarget if needed
            if (!element) element = e.currentTarget;
            
            // Only proceed if we have an element and the required properties
            if (element && e.clientX !== undefined) {
              const rect = element.getBoundingClientRect();
              if (rect && rect.width > 0) {
                // Calculate position relative to the element's left edge
                position = (e.clientX - rect.left) / rect.width;
                // Ensure position is within 0-1 range
                position = Math.max(0, Math.min(1, position));
                console.log(`Position from element: ${position.toFixed(2)}, width: ${rect.width}px`);
              } else {
                console.log("Element rect invalid or has zero width");
              }
            }
          } catch (err) {
            console.error("Error using target/currentTarget:", err);
          }
        }
        
        // If target-based positioning failed, use a relative approach
        if (position === 0 && e && typeof e.clientX !== 'undefined') {
          console.log("Falling back to relative positioning");
          
          // If we haven't initialized tracking data yet
          if (!newAsset._scrubberData) {
            newAsset._scrubberData = {
              // Start with the current position
              startX: e.clientX,
              // Assume a reasonable default width
              width: 200
            };
          }
          
          // Calculate position based on movement from start point
          const distance = e.clientX - newAsset._scrubberData.startX;
          position = 0.5 + (distance / newAsset._scrubberData.width);
          
          // Constrain to 0-1 range
          position = Math.max(0, Math.min(1, position));
          
          console.log(`Relative position: ${position.toFixed(2)}, distance: ${distance}px`);
        } 
        // Last resort - just use 50% if we couldn't determine a position
        else if (position === 0) {
          position = 0.5;
          console.log("Using default center position");
        }
        
        // Calculate time and frame
        const timePosition = position * duration;
        const frameNumber = Math.floor(position * durationFrames);
        
        // Show loading state
        newAsset.thumbnail = null; // Clear the thumbnail to make HTML visible
        
        // Extract the frame using the asset's method
        const frameDataUrl = await newAsset.extractFrameAt(timePosition);
        
        if (!frameDataUrl) {
          console.log("no frame found at time ", timePosition)
          // Use original thumbnail as fallback
          newAsset.thumbnail = gyre.assetManager.serverURL + "/" + result.asset.meta.thumbnailPath;
          newAsset.thumbnailHTML = null;
          gyre.assetManager.refresh();
          return;
        }
        
        // Update thumbnail with frame image and overlay info
        newAsset.thumbnailHTML = `
          <div class="scrubber-container" style="
            position: relative;
            width: 100%;
            height: 100%;
            background-color: #000;
            overflow: hidden;
          ">
            <img src="${frameDataUrl}" style="
              width: 100%;
              height: 100%;
              object-fit: contain;
            " />         
          </div>
        `;
        gyre.assetManager.refresh();
      } catch (error) {
        console.error("Error in thumbnailMouseMove:", error);
        // Fall back to thumbnail
        newAsset.thumbnailHTML = null;
        newAsset.thumbnail = gyre.assetManager.serverURL + "/" + result.asset.meta.thumbnailPath;
        gyre.assetManager.refresh();
      }
    };
    
    // Handle mouse leave to restore thumbnail
    newAsset.thumbnailMouseLeave = () => {
      newAsset.thumbnailHTML = null;
      newAsset.thumbnail = gyre.assetManager.serverURL + "/" + result.asset.meta.thumbnailPath;
      gyre.assetManager.refresh();
    };
    

  }

  // Helper function to format time as MM:SS.ms
  function formatTime(seconds) {
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    const ms = Math.floor((seconds % 1) * 100);
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}.${ms.toString().padStart(2, '0')}`;
  }

  export async function importFileAsAsset(file, currentFolder) {
    let gyre = globalThis.gyre;

    let name = file.name.replace(/\.[^/.]+$/, "");
    const newAsset = {
      name: name,
      id: crypto.randomUUID(),
      type: "fds-asset-video",   
      thumbnailHTML: "0%"
    };

    // Create a promise that will resolve when the video is fully loaded
    const assetLoadPromise = new Promise((resolve) => {
      globalThis.gyre.assetManager.serverUploadAsset(
        file, 
        newAsset,
        (progress) => handleUploadProgress(newAsset, progress),
        (result) => () => {
          console.log("Upload complete, initializing video player");
          handleUploadResult(newAsset, result);
          
          // Wait for video to load completely
          if (newAsset.videoElement) {
            const checkLoaded = () => {
              if (newAsset.videoLoaded) {
                console.log("Video loading complete in importFileAsAsset");
                resolve(newAsset);
              } else {
                console.log("Video still loading in importFileAsAsset, checking again in 500ms");
                setTimeout(checkLoaded, 500);
              }
            };
            
            // Start checking
            checkLoaded();
          } else {
            console.log("No video element created, resolving immediately");
            resolve(newAsset);
          }
          
          // Show initial thumbnail
          gyre.assetManager.refresh();
        }
      );
    });

    currentFolder.children.push(newAsset);
    
    // Return the asset immediately, but the promise will resolve when fully loaded
    return newAsset;
  }

  export async function initAssetsOverview(asset) {
    // Prfen, ob das Asset bereits initialisiert wurde
    if (asset.initialized) {
      console.log("Asset already initialized, skipping");
      return asset;
    }
    
    let gyre = globalThis.gyre;
    if (asset?.meta?.thumbnailPath) {
      // Markieren als initialisiert, um Mehrfachaufrufe zu vermeiden
      asset.initialized = true;
      
      // Setze Thumbnail zuerst
      asset.thumbnail = gyre.assetManager.serverURL + "/" + asset.meta.thumbnailPath;
      
      // Create a deep copy of the asset to ensure references are properly preserved
      let result = {asset: JSON.parse(JSON.stringify(asset))};
      
      // Initialisiere Video und andere Elemente
      handleUploadResult(asset, result);
      
      console.log("initAssetsOverview: finished setup, checking video status...");
      
      // Maximale Anzahl von Versuchen definieren (30 Sekunden)
      const maxAttempts = 60;
      let attempts = 0;
      
      // Warten auf Video-Ladung mit Timer
      while (asset.videoElement && !asset.videoLoaded && attempts < maxAttempts) {
        console.log(`Waiting for video to load... Attempt ${attempts + 1}/${maxAttempts}`);
        await new Promise(resolve => setTimeout(resolve, 500));
        attempts++;
      }
      
      if (asset.videoLoaded) {
        console.log("Video successfully loaded!");
      } else if (attempts >= maxAttempts) {
        console.warn("Timed out waiting for video to load after 30 seconds");
      } else {
        console.log("No video element or already loaded");
      }
      
      return asset;
    }
  }
</script>